.skills-section {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.jumbotron {
    width: 80%;
    height: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    transition: 0.3s;
}

.skills-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 20px;
}

.skill {
    width: 200px;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 10px;
    background-color: #0b0bee47;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    transition: 0.3s;
}

.skill:hover {
    transform: scale(1.1);
}

.skill i {
    font-size: 50px;
    color: #fff;
}

.skill p {
    font-size: 20px;
    color: #fff;
    margin-top: 10px;
}

@media screen and (max-width: 768px) {
    .skills-section {
        height: auto;
    }

    .jumbotron {
        width: 90%;
        height: auto;
    }

    .skills-container {
        flex-direction: column;
    }

    .skill {
        width: 100%;
        height: 150px;
    }

    .skill i {
        font-size: 30px;
    }

    .skill p {
        font-size: 15px;
    }
}