/* home.css */

.home-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
        url(../image/background.jpg) center/cover no-repeat fixed;
    /* Set your preferred background color */
}

.home-content {
    text-align: center;
    color: #fff;
    /* Set your preferred text color */
}

.text-1-home {
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 10px;
}

.text-2-home {
    font-size: 36px;
    font-weight: bold;
    color: #61dafb;
    /* Set your preferred color for the name */
    margin-bottom: 20px;
}

.text-3-home {
    font-size: 35px;
    margin-bottom: 20px;
}

.text-3-home span {
    font-weight: bold;
    color: #61dafb;
}

.scroll-down {
    margin-top: 30px;
}

.scroll-down a {
    text-decoration: none;
    color: #007bff;
    /* Set your preferred color for the arrow */
}

.scroll-down i {
    font-size: 24px;
}

.scroll-down i {
    color: #61dafb;
    /* Set your preferred color for the arrow when hovered */
    animation: animate 2s ease-in-out infinite;
}

@keyframes animate {
    0% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(10px);
    }

    100% {
        transform: translateY(0);
    }
}



/* Media Query for Responsive Design */
@media only screen and (max-width: 768px) {
    .text-2-home {
        font-size: 28px;
    }

    .text-3-home {
        font-size: 16px;
    }
}