/* Reset some default styles and apply a box-sizing border-box */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

/* Style the overall contact section */
.contact-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 50px;
}

/* Style the container for the contact information and social links */
.contact-container {
    flex: 1;
    max-width: 600px;
}

.contact-container h1 {
    font-size: 2em;
    margin-bottom: 20px;
}

.contact-container p {
    font-size: 1.2em;
    margin-bottom: 30px;
}

/* Style the social links */
.social-links {
    display: flex;
    gap: 20px;
}

.social-links a {
    text-decoration: none;
    color: #fff;
    font-size: 1.5em;
}

/* Style the form container */
.form-container {
    flex: 1;
    max-width: 600px;
    margin-left: 50px;
}

/* Style the form */
form {
    display: grid;
    gap: 20px;
}

/* Style form groups */
.form-group {
    display: grid;
    gap: 10px;
}

/* Style labels */
label {
    font-size: 1.2em;
}

/* Style input and textarea */
input,
textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    outline: none;
    font-family: 'Roboto Mono', monospace;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    background-color: #0f0f0f;
    color: #fff;
}

/* Style the submit button */
button {
    background-color: #449d44;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1.2em;
}

/* Hover effect for the submit button */
button:hover {
    background-color: #398439;
}

@media (max-width: 768px) {
    .contact-section {
        flex-direction: column;
        padding: 20px;
    }

    .form-container {
        margin-left: 0;
        margin-top: 20px;
        width: 100%;
    }
}