/* Footer.css */

.footer-section {
    background-color: #000000;
    color: #fff;
    padding: 20px 0;
    text-align: center;
}

.footer-content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer-text {
    margin-bottom: 10px;
}

.heart {
    color: #e74c3c;
}

.social {
    margin-top: 20px;
}

.social-link {
    display: inline-block;
    margin: 0 10px;
    color: #fff;
    font-size: 20px;
    transition: color 0.3s ease-in-out;
}

.social-link:hover {
    color: #3498db;
}