.projects-section {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    padding: 50px 0;
}

.project-info {
    padding: 100px 40px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    position: relative;
    top: -50px;
}

.project-title {
    font-weight: 500;
    font-size: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: white;
    width: 100%;
}

.project-links {
    display: flex;
    gap: 15px;
    width: 100%;
}

.project-link {
    font-size: 20px;
    color: white;
    transition: all 0.3s;
}

.project-link i {
    font-size: 20px;
}

.lighter {
    font-weight: lighter;
    color: grey;
    font-size: 13px;
    width: 100%;
}

.flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.tag {
    font-weight: lighter;
    color: grey;
    font-size: 12px;
}

/*DELETE THIS TWO LINE*/
.delete {
    /* background-color: #b2b2fd; */
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.card-img div {
    width: 90%;
}

/*IF USING IMAGES*/

.card {
    background-color: #161616;
    color: black;
    width: 300px;
    max-height: 330px;
    border-radius: 8px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
        rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    transition: all 0.5s;
    margin: 15px auto;
}

.card-img {
    position: relative;
    top: -20px;
    height: 100px;
    display: flex;
    justify-content: center;
}

/* Change the .card-img div to .card-img img to use img*/
.card-img a,
.card-img div {
    height: 150px;
    width: 90%;
    /* Change this width here to change the width of the color/image */
    object-fit: cover;
    border-radius: 8px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.card-imgs {
    transition: all 0.5s;
}