/* resume.css */

/* Global Styles */
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Container for the entire resume */
.resume-section {
    max-width: 800px;
    margin: 50px auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Header Section */
.header {
    text-align: center;
}

.profile-picture {
    height: 170px;
    border-radius: 10px;
    margin-bottom: 10px;
}

.name {
    font-size: 24px;
    font-weight: bold;
}

.bio {
    font-style: italic;
    color: #666;
}

.resume-information {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.contact-information {
    width: 90%;

}

.contact-1 {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;

}

.contact-title {
    font-weight: bold;
    color: #ffffff;

}

.contact-content a {
    color: #ffffff;
    text-decoration: none;
}


/* Education Section */
.education-section {
    margin-top: 20px;
}

.education-section h2 {
    color: #333;
}

.education-section h3 {
    margin-top: 10px;
    color: #ffffff;
}

/* Skills Section */
.education-section ul {
    list-style: none;
    padding: 0;
}

.education-section ul li {
    color: #a8a6a6;
}

/* Marksheet Table */
table {
    width: 100%;
    margin-top: 20px;
    border-collapse: collapse;
}

th,
td {
    border: 1px solid #ddd;
    padding: 10px;
    text-align: left;
}

th {
    background-color: #131212;
    color: #fff;
}