/* About.css */

.about-section {
    background-color: #000000;
    /* Set a background color for the section */
    padding: 40px 20px;
    /* Add some padding to the section */
}

.about-content {
    max-width: 1200px;
    /* Set a maximum width for the content */
    margin: 0 auto;
    /* Center the content horizontally */
    display: flex;
    justify-content: space-between;
    /* Space the content evenly */
    align-items: center;
}

.about-text {
    flex: 1;
    /* Take up remaining space in the container */
}

.about-heading h1 {
    color: #ded8d8;
    padding-bottom: 10px;
    /* Set the heading color */
}

.about-para{
    width: 90%;
    text-align: justify;
}

.about-para p {
    font-size: 16px;
    /* Set the paragraph font size */
    line-height: 1.6;
    /* Set the line height for better readability */
    color: #777;
    /* Set the paragraph color */
}

.about-image img {
    width: 100%;
    /* Make the image responsive within its container */
    border-radius: 8px;
    /* Add a slight border radius to the image */
}

/* Media queries for responsiveness */
@media screen and (max-width: 768px) {
    .about-content {
        flex-direction: column;
        /* Stack the content vertically on smaller screens */
        text-align: center;
        /* Center-align text on smaller screens */
    }

    .about-image img {
        margin-top: 20px;
        /* Add some margin at the top for better spacing */
    }
}