/* CustomNavbar.css */

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap');

.custom-navbar {
    background-color: #000000;
    color: white;
    height: 80px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2);
}

.custom-navbar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1300px;
    padding: 0 50px;
}

.custom-navbar-logo {
    color: white;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 24px;
    display: flex;
    width: 100%;
    padding: 0 20px;
    align-items: center;
    font-weight: 600;
    letter-spacing: 4px;
    font-family: 'Poppins', sans-serif;
}

.custom-menu-icon {
    display: none;
    cursor: pointer;
    height: 30px;
    width: 30px;
}

.custom-menu-icon.open .custom-menu-line:nth-child(1) {
    transform: rotate(-45deg) translate(-5px, 6px);
}

.custom-menu-icon.open .custom-menu-line:nth-child(2) {
    opacity: 0;
}

.custom-menu-icon.open .custom-menu-line:nth-child(3) {
    transform: rotate(45deg) translate(-5px, -6px);
}

.custom-menu-line {
    width: 30px;
    height: 3px;
    background-color: #ffffff;
    margin: 5px;
    transition: all 0.3s ease-in-out;
}

.custom-nav-menu {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    text-align: center;
}

.custom-nav-links {
    color: white;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0 1rem;
    height: 100%;
    font-size: 24px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    font-family: 'Roboto Mono', monospace;
}

.custom-nav-links:hover {
    color: #61dafb;
}

@media screen and (max-width: 768px) {
    .custom-navbar-container {
        padding: 0 20px;
    }

    .custom-navbar-logo {
        font-size: 20px;
    }

    .custom-menu-icon {
        display: block;
    }

    .custom-nav-menu {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 80px;
        left: 0;
        width: -webkit-fill-available;
        background-color: #000000;
        opacity: 0;
        visibility: hidden;
        transition: all 0.3s ease-in-out;
    }

    .custom-nav-menu.open {
        opacity: 1;
        visibility: visible;
    }

    .custom-nav-links {
        width: 100%;
        padding-bottom: 1rem;
    }
}
